import Axios from "axios";
import moment from "moment";
export function getSearchParametersFromHRef(href) {
  if (!href) return {};
  var search = {};
  var s1 = href.split("?");
  if (s1.length > 1) {
    var s2 = s1[1].split("&");
    for (let index = 0; index < s2.length; index++) {
      const s3 = s2[index].split("=");
      search[s3[0]] = decodeURIComponent(s3[1]);
    }
  }
  return search;
}

export function getSearchParametersFromHash(href) {
  if (!href) return {};
  var search = {};
  var s1 = href.split("#");
  if (s1.length > 1) {
    var s2 = s1[1].split("&");
    for (let index = 0; index < s2.length; index++) {
      const s3 = s2[index].split("=");
      search[s3[0]] = decodeURIComponent(s3[1]);
    }
  }
  return search;
}

const GETACCESSTOKEN = "getAccessToken";
const RESTOREURL = "restoreUrl";
const GETGRAPHUSER = "getGraphUser";
const LOGIN = "login";
const ACCESSTOKEN = "access_token";
const ACCESSEXPIRES = "access_expires";

function isAccessTokenExpired() {
  try {
    var exp = sessionStorage.getItem(ACCESSEXPIRES);
    var expiresIn = moment(exp);
    var now = moment();
    var x = now.diff(expiresIn);
    return x > 0 ? true : false;
  } catch (error) {
    return true;
  }
}

/**
 * Based on session storage - if no auth token is present, or if is to old, a auth sequence is initiated
 * @param {*} config 
 * @param {*} callBackNotifyIfInteractionIsRequired 
 */
export function authenticate(config, callBackNotifyIfInteractionIsRequired) {
  // https://docs.microsoft.com/en-us/azure/active-directory/develop/v2-oauth2-implicit-grant-flow

  return new Promise((resolve, reject) => {
    var redirect_uri =
      (window.location.hostname === "localhost"
        ? window.location.protocol
        : "https:") +
      "//" +
      window.location.hostname +
      (window.location.port !== 80 &&
      window.location.port !== 443 &&
      window.location.port !== ""
        ? ":" + window.location.port
        : "") +
      "/";
    // window.location.pathname;

    var host = `https://login.microsoftonline.com/${config.directoryId}/oauth2/v2.0/authorize`;
    var client_id = config.clientId;
    var response_type = "id_token";
    var response_mode = "fragment";
    var scope = config.scopes;

    var seq = ""; // sessionStorage.getItem("authseq");
    var search = getSearchParametersFromHash(window.location.href);

    var search2 = getSearchParametersFromHRef(window.location.href);
    //debugger

    if (search.id_token) {
      seq = GETACCESSTOKEN;
    } else if (search.access_token) {
      seq = RESTOREURL;
    } else if (search.login) {
      seq = LOGIN;
    } else if (seq === "") {
      if (sessionStorage.getItem(ACCESSTOKEN)) {
        seq = isAccessTokenExpired() ? GETACCESSTOKEN : GETGRAPHUSER;
      } else {
        seq = GETACCESSTOKEN;
      }
    }

    if (search.error || search2.error) {
      var e = search.error ? search.error : search2.error;
      var handled = false;
      switch (e) {
        case "interaction_required":
          if (callBackNotifyIfInteractionIsRequired) {
            callBackNotifyIfInteractionIsRequired();
          }
          sessionStorage.removeItem("accesstoken");
          sessionStorage.removeItem("idtoken");

          var url = `${host}?client_id=${client_id}&response_type=${response_type}&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=openid&nonce=1`;
          window.location.href = url;
          return;
          break;
        case "consent_required":
          if (callBackNotifyIfInteractionIsRequired) {
            callBackNotifyIfInteractionIsRequired();
          }
          //debugger;
          sessionStorage.removeItem("accesstoken");
          sessionStorage.removeItem("idtoken");

          var url = `${host}?client_id=${client_id}&prompt=consent&response_type=token&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=openid&nonce=1`;
          window.location.href = url;
          return;
          break;
        case "invalid_request":
          
          break
        default:
          if (sessionStorage.setItem("returnto", "/"))
            window.location.href = "#login";
          return;
          handled = true;
          break;
      }
      if (!handled) {
        alert(
          search.error_description
            ? search.error_description
            : search2.error_description
        );
        //window.location.href = sessionStorage.getItem("returnto");
        reject(search.error_description)
        return;
      }
    }

    switch (seq) {
      case LOGIN:
        sessionStorage.setItem("returnto", window.location.href);
      
        var url = `${host}?client_id=${client_id}&prompt=none&response_type=token&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=${scope}&nonce=1`;
        window.location.href = url;
        return;
        break;
      case GETACCESSTOKEN:
        sessionStorage.setItem("returnto", window.location.href);
    
        var url = `${host}?client_id=${client_id}&prompt=none&response_type=token&redirect_uri=${redirect_uri}&response_mode=${response_mode}&scope=${scope}`;
        window.location.href = url;
        return;
        break;
      case GETGRAPHUSER:
        sessionStorage.removeItem("returnto");

        Axios.get("https://graph.microsoft.com/v1.0/me", {
          headers: {
            "content-type": "application/json",
            authorization: `Bearer ${sessionStorage.getItem(ACCESSTOKEN)}`,
          },
        })
          .then((result) => {
            resolve({ user: result.data });
          })
          .catch((error) => {
            var txt = error.response.data.error.message;
            if (
              error &&
              error.response &&
              error.response.data &&
              error.response.data.error
            ) {
              //alert(error.response.data.error.message)
              resolve({ error: error.response.data.error.message });
            } else {
              resolve({ error });
            }
          });
        break;

      case RESTOREURL:
        if (search.access_token) {
          sessionStorage.setItem(ACCESSTOKEN, search.access_token);

          var expires = moment().add(search.expires_in, "s");

          sessionStorage.setItem(ACCESSEXPIRES, expires.toISOString());
          if (sessionStorage.getItem("returnto"))
            window.location.href = sessionStorage.getItem("returnto");
          return;
        } else {
          window.location.reload();
        }
        resolve();
        break;

      default:
        resolve({
          error:
            "Sorry to have put you in this situation, but our authentication logic is not able to detect the next step to taka so we cannot let you in",
        });
        break;
    }
  });
}
