import React from "react";
import ReactDOM from "react-dom";
import "./index.css";
import App from "./App";
import Onboard from "./Onboard";
import config from "./config";
import { authenticate } from "./authenticate";

import { FluentCustomizations } from "@uifabric/fluent-theme";
import { Customizer, mergeStyles, Fabric } from "office-ui-fabric-react";
import { loadTheme } from "@fluentui/react";
import { EVENT, who, trace } from "./helpers/trace";

import { Router, Link } from "@reach/router";
import {
  MessageBarButton,
  DefaultButton,
} from "office-ui-fabric-react/lib/Button";



export default class ErrorBoundary extends React.Component {
  constructor(props) {
    super(props);
    this.state = { hasError: false };
  }

  static getDerivedStateFromError(error) {
    // Update state so the next render will show the fallback UI.
    return { hasError: true };
  }

  componentDidCatch(error, info) {
    // You can also log the error to an error reporting service
    this.setState({ error, info });
    console.log("componentDidCatch", error, info);
    //logErrorToMyService(error, info);
  }

  render() {
    if (this.state.hasError) {
      // You can render any custom fallback UI
      return (
        <div className="pageBody">
          <h1 className="errorCenterHeader">
            Sorry! Something not working as expected
          </h1>
          {this.state.error && this.state.error.message && (
            <h2>{this.state.error.message}</h2>
          )}
          <div className="errorCenterHeader">
            <div>
              <DefaultButton
                style={{ margin: "16px", width: "300px" }}
                primary
                text="Visit our Help Center"
                onClick={() => {
                  window.open(
                    "https://jumpto365.zendesk.com/hc/en-us",
                    "_blank"
                  );
                }}
              />
            </div>
            <div>
              <DefaultButton
                style={{ margin: "16px", width: "300px" }}
                text="Check service availabiliy"
                onClick={() => {
                  window.open("https://uptime.com/s/overview", "_blank");
                }}
              />
            </div>
            {/* <div>
              <DefaultButton
                style={{ margin: "16px", width: "300px" }}
                text="File a support request"
                onClick={() => {
                  if (window.zE) {
                    zE(function() {
                      debugger;
                    });
                  }
                }}
              />
            </div> */}

            <div>
              <DefaultButton
                style={{ margin: "16px", width: "300px" }}
                text="View error details"
                onClick={() => {
                  this.setState({ viewDetails: true });
                }}
              />
            </div>
          </div>
          {this.state.info &&
          this.state.info.componentStack &&
          this.state.viewDetails ? (
            <div>
              <textarea
                style={{ width: "100%" }}
                readOnly
                rows="30"
                cols="80"
                value={this.state.info.componentStack}
              />{" "}
            </div>
          ) : null}
        </div>
      );
    }

    return this.props.children
  }
}

var x = loadTheme({
  defaultFontStyle: {
    fontFamily: "Monaco, Menlo, Consolas",
    fontWeight: "regular",
  },
  fonts: {
    small: {
      fontSize: "11px",
    },
    medium: {
      fontSize: "13px",
    },
    large: {
      fontSize: "20px",
      fontWeight: "semibold",
    },
    xLarge: {
      fontSize: "22px",
      fontWeight: "semibold",
    },
  },
});

